
	import {
		IonIcon,
		IonHeader,
		IonToolbar,
		IonTitle,
		IonButtons,
		IonButton,
		IonBackButton,
		IonMenuButton,
	} from '@ionic/vue';
	import { settings, settingsOutline, chevronBackOutline } from 'ionicons/icons';
	import { useRouter } from 'vue-router';
	import { defineComponent } from 'vue';

	export default defineComponent({
		name: 'Header',
		components: {
			IonIcon,
			IonHeader,
			IonToolbar,
			IonTitle,
			IonButtons,
			IonButton,
			IonBackButton,
			IonMenuButton,
			
		},
		props: {
			title: String,
			isNotBack: Boolean,
			isSettings: Boolean,
			isCustomBack: Boolean,

		},
		setup() {
			const router = useRouter();
			
			const goBack = () => {
				router.back();
			};

			return {
				goBack,
				settings,
				settingsOutline,
				chevronBackOutline,

			};
		},
		
	});
